import React from "react";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addItem } from "./store";
import { Link } from "react-router-dom";
// 기능들

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css/pagination";
import { Pagination } from "swiper";
import "swiper/css";
// 스와이퍼

import Form from "react-bootstrap/Form";
import { Nav } from "react-bootstrap";
// 부트스트랩

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
// 폰트어썸


// 갖고온 타입
import {TopSellerType} from "./App"


//갖고온 타입으로 배열추가해서 새로운 타입을 만듦.
interface ProductProps {
  seller: TopSellerType[]; // 배열로 수정
}



// 정보 타입
interface CustomerListType {
  name: string;
  message: string;
  size: string;
}



// 프롭스 탭, 서브 이미지 타입
interface PropsType {
  tap: number;
  product: {
    subImgProd1: string;
    subImgProd2: string;
    subImgProd3: string;
    subImgProd4: string;
    subImgProd5: string;
    
  };
}


// 서브페이지
function Product(props:ProductProps): JSX.Element {
  let [fade2, setFade2] = useState('')


  useEffect(()=>{

    window.scrollTo(0, 0);

         setFade2('end')

         return ()=>{

              setFade2('')

         }

},[])


  // window.scrollTo(0, 0);


  let { id } = useParams(); // 사이트 주소 파라미터 값& 해당 상품의 사이트

  const [product, setProduct] = useState<TopSellerType | null>({
  id: 0,
  imgUrl: "",
  title: "",
  code: "",
  price: "",
  subImgProd1: "",
  subImgProd2: "",
  subImgProd3: "",
  subImgProd4: "",
  subImgProd5: "",}); //누른 상품이 올 자리

  let [tap, setTap] = useState<number>(0);

  let dispatch = useDispatch();
  // 스토어에서 함수 불러옴.

  useEffect(() => {
    window.scrollTo(0, 0);

    // id를 사용하여 상품 정보 가져옴
    const GetProduct = props.seller.find((item) => item.id === parseInt(id|| "")); //seller의 id와 파라미터로 갖고온 상품의 id를 비교함. 그리고 일치한 id가 있는 데이터를 모두 GetProduct에 담음. =메인에서 내가 누른 값이 담김.
    setProduct(GetProduct || null); //빈 공간을 갖고온 상품의 데이터로 채움.
  }, [id, setProduct]); //id의 값과 상품 변수가 바뀔 때마다 useEffect 실행.

  if (product === null) {
    /*변수의 값이 null일 때 if문으로 없을 경우에 대한 경우를 적어야함.*/
    return <div>Loading...</div>;
  }

  return (
    <div className="start ex">
      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <Swiper
              spaceBetween={30}
              pagination={{
                clickable: true,
              }}
              modules={[Pagination]}
              className="mySwiper"
            >
              <SwiperSlide>
                <img
                  src={"/types2/img/top_seller/" + product.imgUrl} //내가 입력한 값의 seller가 나옴
                  width="100%"
                />
              </SwiperSlide>
              <SwiperSlide>
                <img
                  src={"/types2/img/top_seller/" + product.imgUrl2} //내가 입력한 값의 seller가 나옴
                  width="100%"
                />
              </SwiperSlide>
            </Swiper>
          </div>

          <div className="col-md-6">
            <div className="productBox">
              <div className="pro_info">
                <div className="Title_box">
                  <h2>{product.title}</h2>
                </div>
                {/* 상품명 */}

                <div className="Price_box">
                  <h1>{product.price}원</h1>
                </div>
                {/* 가격 */}

                <div className="Version_box">
                  <h3>Color</h3>
                  <ul>
                    <li className="thumb">
                      <img src="/types2/img/pro_mini_clothes1.jpg" />
                    </li>
                    <li className="thumb">
                      <img src="/types2/img/pro_mini_clothes2.jpg" />
                    </li>
                    <li className="thumb">
                      <img src="/types2/img/pro_mini_clothes3.jpg" />
                    </li>
                  </ul>
                </div>
              </div>

              <div className="pro_option">
                <table>
                  <tr>
                    <th>사이즈</th>
                    <td>
                      <Form.Select
                        aria-label="Default select example"
                        style={{ width: "100%" }}
                      >
                        <option>- [필수] SELECT OPTION -</option>
                        <option>--------------------------</option>
                        <optgroup label="사이즈">
                          <option value="1">S</option>
                          <option value="2">M</option>
                          <option value="3">L</option>
                        </optgroup>
                      </Form.Select>
                    </td>
                  </tr>
                </table>
              </div>

              <h5>(최소주문수량 1개이상)</h5>

              <div className="Btn_Box">
                <button
                  type="button"
                  className="btn btn-success"
                  onClick={() => {
                    dispatch(
                      addItem({
                        id: product.id,
                        code: product.code,
                        imgUrl: product.imgUrl,
                        name: product.title,
                        count: 1,
                      })
                    );
                  }}
                >
                  BUY IT NOW
                </button>

                <Link to="/cart">
                  <button type="button" className="btn btn-outline-success">
                    CART
                  </button>
                </Link>

                <Link to="/cart">
                  <button
                    type="button"
                    className="btn btn-outline-light"
                    style={{ border: "1px solid #ddd" }}
                  >
                    WISH LIST
                  </button>
                </Link>
              </div>
            </div>
          </div>

          {/* row */}
        </div>

        {/* <Nav fill variant="tabs" className="NavTabs">
          <Nav.Item>
            <Nav.Link  eventKey="link-1">상세정보</Nav.Link>
          </Nav.Item>

          <Nav.Item>
            <Nav.Link eventKey="link-2">
              상품후기<span className="reviewNum">26</span>
            </Nav.Link>
          </Nav.Item>

          <Nav.Item>
            <Nav.Link eventKey="link-3">배송/교환/반품</Nav.Link>
          </Nav.Item>
        </Nav> */}

        <Nav
          variant="tabs"
          defaultActiveKey="link0"
          style={{ marginTop: "100px" }}
        >
          <Nav.Item style={{ width: "33%", textAlign: "center" }}>
            <Nav.Link
              onClick={() => {
                setTap(0);
              }}
              eventKey="link0"
            >
              상세정보
            </Nav.Link>
          </Nav.Item>

          <Nav.Item style={{ width: "33%", textAlign: "center" }}>
            <Nav.Link
              onClick={() => {
                setTap(1);
              }}
              eventKey="link1"
            >
              상품후기
            </Nav.Link>
          </Nav.Item>

          <Nav.Item style={{ width: "33%", textAlign: "center" }}>
            <Nav.Link
              onClick={() => {
                setTap(2);
              }}
              eventKey="link2"
            >
              배송/교환/반품
            </Nav.Link>
          </Nav.Item>
        </Nav>


        {/* 누르는 버튼에 따라 내용 뜸 */}
        <TabContent tap={tap} product={product} ></TabContent>

        {/* container */}
      </div>
    </div>
  );
}




// 요소 나타나게 하는 전체 컴포넌트
function TabContent(props:PropsType): JSX.Element {
  return [
    // 상품 상세정보
    <div className="container subImgProd">
      <div className="col-md-12">
        <div style={{ width: "900px", margin: "80px auto" }}>
          <img
            src="/types2/img/sub_product/sub_img_01.jpg"
            style={{ cursor: "pointer" }}
          />

          <img src="/types2/img/sub_product/sub_img_02_MB_banner.jpg" />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd1} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd2} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd3} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd4} />

          <img src={"/types2/img/sub_product/" + props.product.subImgProd5} />

          <img src="/types2/img/sub_product/sub_img_03_clean_tip.jpg" />

          <img src="/types2/img/sub_product/sub_img_04_malbon.jpg" />

          <img src="/types2/img/sub_product/sub_img_05_malbon_delivery.jpg" />
        </div>
      </div>
    </div>,

    <ProReview />,

    <Delivery />,
  ][props.tap];
}







