// 배송/교환/반품 컴포넌트
function Delivery() {
  return (
    <div className="container" style={{ marginBottom: "100px" }}>
      <div className="col-md-12">
        <div className="info_box">
          <h3>PAYMENT INFO</h3>
          <div className="txt_info_box">
            <span>
              고액결제의 경우 안전을 위해 카드사에서 확인전화를 드릴 수도
              있습니다. 확인과정에서 도난 카드의 사용이나 타인 명의의 주문등
              정상적인 주문이 아니라고 판단될 경우 임의로 주문을 보류 또는
              취소할 수 있습니다.
              <br />
              <br />
              무통장 입금은 상품 구매 대금은 PC뱅킹, 인터넷뱅킹, 텔레뱅킹 혹은
              가까운 은행에서 직접 입금하시면 됩니다.
              <br />
              주문시 입력한 입금자명과 실제입금자의 성명이 반드시 일치하여야
              하며, 7일 이내로 입금을 하셔야 하며 입금되지 않은 주문은 자동취소
              됩니다.
            </span>
          </div>

          <h3>DELIVERY INFO</h3>
          <div className="txt_info_box">
            배송 방법 : 택배 <br />
            배송 지역 : 전국지역 <br />
            배송 비용 : 무료 <br />
            배송 기간 : 3일 ~ 5일 <br />
            배송 안내 :
          </div>

          <h3>EXCHANGE INFO</h3>
          <div className="txt_info_box">
            <span>
              <strong>
                교환 및 반품 주소 <br />{" "}
              </strong>
              - [15657] 경기도 안산시 단원구 시화호수로 835 (성곡동) 로지스밸리
              3층
              <strong>
                <br />
                <br />
                교환 및 반품이 가능한 경우 <br />
              </strong>
              - 상품을 공급 받으신 날로부터 7일이내 단, 가전제품의 <br />
              &nbsp; 경우 포장을 개봉하였거나 포장이 훼손되어 상품가치가 상실된
              경우에는 교환/반품이 불가능합니다. <br />
              - 공급받으신 상품 및 용역의 내용이 표시.광고 내용과 <br />
              &nbsp; 다르거나 다르게 이행된 경우에는 공급받은 날로부터 3월이내,
              그사실을 알게 된 날로부터 30일이내
              <strong>
                <br />
                <br />
                교환 및 반품이 불가능한 경우 <br />
              </strong>
              - 고객님의 책임 있는 사유로 상품등이 멸실 또는 훼손된 경우. 단,
              상품의 내용을 확인하기 위하여 <br />
              &nbsp; 포장 등을 훼손한 경우는 제외 - 포장을 개봉하였거나 포장이
              훼손되어 상품가치가 상실된 경우
              <br />
              &nbsp; (예 : 가전제품, 식품, 음반 등, 단 액정화면이 부착된 노트북,
              LCD모니터, 디지털 카메라 등의 불량화소에 <br />
              &nbsp; 따른 반품/교환은 제조사 기준에 따릅니다.) <br />
              - 고객님의 사용 또는 일부 소비에 의하여 상품의 가치가 현저히
              감소한 경우 단, 화장품등의 경우 시용제품을 <br />
              &nbsp; 제공한 경우에 한 합니다. <br />
              - 시간의 경과에 의하여 재판매가 곤란할 정도로 상품등의 가치가
              현저히 감소한 경우
              <br />
              - 복제가 가능한 상품등의 포장을 훼손한 경우 <br />
              &nbsp; (자세한 내용은 고객만족센터 1:1 E-MAIL상담을 이용해 주시기
              바랍니다.)
              <br /> <br /> ※ 고객님의 마음이 바뀌어 교환, 반품을 하실 경우
              상품반송 비용은 고객님께서 부담하셔야 합니다. <br />
              &nbsp; (색상 교환, 사이즈 교환 등 포함)
            </span>
          </div>

          <h3>SERVICE INFO</h3>
        </div>
      </div>
    </div>
  );
}



