//slick 버튼 스타일 컴포넌트 
function SampleNextArrow(props:ArrowProps) {

  const { className, style, onClick } = props;

  
  return (
    <div
      className={className}
      style={{ ...style, display: "block", background: "#ddd", borderRadius:"50px",  }}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props:ArrowProps) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: "block", background: "#ddd", borderRadius:"50px" }}
      onClick={onClick}
    />
  );
}




//TOP SELLER 인기 상품 컴포넌트 -----------------------------------------------------------
function TopSeller(props:TopSellerProps): JSX.Element {

  // 사진 호버 펄스
  const [isListHover, setIsListHover] = useState(false);


  let navigate = useNavigate();

  const handleProductClick = () => {
    navigate("/Product/" + props.seller.id);
  };


  return (
    <>
      <div className="col-md-3" style={{ marginTop: "40px" }}>
        <Nav.Link
          onClick={handleProductClick}
        >
          <div className="imgBox" style={{ width: "100%" }} 
          onMouseOver={() => setIsListHover(true)}
          onMouseOut={() => setIsListHover(false)}>
            <img
              src={isListHover ? "/types2/img/top_seller/" + props.seller.imgUrl2  :"/types2/img/top_seller/" + props.seller.imgUrl}
              style={{ width: "100%" }}
            />
          </div>

          <div className="textBox" style={{ width: "100%", marginTop: "15px" }}>
            <h3 className="h3Style">{props.seller.title}</h3>
            <h4 className="h4Style"> {props.seller.code}</h4>
            <p className="Pstyle">{props.seller.price}원</p>
          </div>
        </Nav.Link>
      </div>
    </>
  );
}



